    export default {
        name: "Search",
        data() {
            return {
                isSearching: true,
				isFocus: false,
				searchText: '',
				tmpSearchText: ''
            }
        },
		
        methods: {
            toSearch() {
                this.isSearching = false
            },
            cancelSearch() {
                this.isSearching = true
				
				
				this.tmpSearchText = this.searchText
				this.searchText = ''
            },
            search(e){
				this.isFocus = false
				this.$EventBus.$emit('removeCover')
                this.$emit('search', e.target.value)
            },
            addCover(){
                this.$EventBus.$emit('addCover')
                
				this.searchText = this.tmpSearchText
            }
        },
		watch:{
			isSearching(value){
				if(value===false){
					this.$nextTick(()=>{
						this.isFocus = true
					})
				}
			}
		}
    }
